function Searchbar({
  title,
  setTitle,
  datas,
  display,
  setDisplay,
  filterWord,
}) {
  const result = filterWord(title, datas);

  return (
    <div className="container">
      <div className="input-container">
        <input
          placeholder="Start typing..."
          type="text"
          onChange={(e) => setTitle(e.target.value)}
          onFocus={() => setDisplay(true)}
        />
      </div>
      <div
        className="search-container"
        style={{
          display: display === false || title === "" ? "none" : "block",
          overflowY: result.length < 7 ? "hidden" : "scroll",
        }}
      >
        {result.map((word) => {
          return (
            <p
              onClick={() => {
                setTitle(word.title);
                setDisplay(false);
              }}
            >
              {word.title}
            </p>
          );
        })}
      </div>
    </div>
  );
}

export default Searchbar;
