function Products({ title, datas, filterWord }) {
  const result = filterWord(title, datas);

  return (
    <div className="products">
      {result.map((word, i) => {
        return (
          <div className="word-container">
            <h2>{Object.keys(word)[0]}</h2>
            <p>{word.title}</p>
            <h2>{Object.keys(word)[1]}</h2>
            <p>{word.type}</p>
            <h2>{Object.keys(word)[2]}</h2>
            <p>{word.description}</p>
            <h2>{Object.keys(word)[3]}</h2>
            <p>{word.filename}</p>
            <h2>{Object.keys(word)[4]}</h2>
            <p>{word.height}</p>
            <h2>{Object.keys(word)[5]}</h2>
            <p>{word.price}</p>
            <h2>{Object.keys(word)[6]}</h2>
            <p>{word.rating}</p>
          </div>
        );
      })}
    </div>
  );
}

export default Products;
