import words from "./words";
import Searchbar from "./components/Searchbar";
import Products from "./components/Products";
import "./App.css";
import { useState } from "react";

function App() {
  const [title, setTitle] = useState("");
  const [display, setDisplay] = useState(true);

  const filterWord = (title, words) =>
    title === ""
      ? []
      : words.filter((data) =>
          data.title.toLowerCase().includes(title.toLowerCase())
        );

  return (
    <div className="App">
      <h1>Search List</h1>
      <div className="focusOut" onClick={() => setDisplay(false)}></div>
      <Searchbar
        title={title}
        setTitle={setTitle}
        datas={words}
        filterWord={filterWord}
        display={display}
        setDisplay={setDisplay}
      />
      <Products filterWord={filterWord} title={title} datas={words} />
    </div>
  );
}

export default App;
